	module.exports = {"data":{"markdownRemark":{"html":"<h2>Electron</h2>\n<p>Electron lets you build cross-platform desktop apps using web technology. You\ncan build desktop apps for Windows, Mac, and most common flavors of Linux.</p>\n<p>Electron is built on Chromium (the open-sourced version of Google Chrome). You\nuse web technologies like HTML, JavaScript, and CSS to build Electron apps. That\nmeans you can use most any web technology you want to build your native desktop\napp! For example, you could use <a href=\"https://medium.freecodecamp.org/building-an-electron-application-with-create-react-app-97945861647c\">React</a> or\n<a href=\"https://scotch.io/tutorials/creating-desktop-applications-with-angularjs-and-github-electron\">Angular</a> to build your first desktop app.</p>\n<p>Additionally, Electron comes with support for auto-updating, crash reporting,\nand native menus.</p>\n<p>Some apps built using Electron include:</p>\n<ul>\n<li>Atom (GitHub’s open-source text-editor)</li>\n<li>Visual Studio Code (Microsoft’s open-source text-editor)</li>\n<li>Slack (A messaging app for teams)</li>\n</ul>\n<p>For more information head to <a href=\"https://electron.atom.io/\">electron.atom.io</a>.</p>","fields":{"slug":"/electron/"},"frontmatter":{"title":"Electron"}}},"pathContext":{"slug":"/electron/"}}

